<template>
    <div class="d-flex justify-content-center" v-if="loading">
        <div class="spinner-border  spinner-border-sm">
        </div>
    </div>
    <div v-else id="panel__wallet" class="text-center">
        <div class="wallet__header p-4 text-center">
            <img src="../../assets/images/Mask-group.svg" class="wallet__header--img" alt="">
            <div class="d-flex flex-column align-items-end justify-content-between h-100">
                <div class="wallet__uppertext d-flex justify-content-center align-items-center">
                    <p class="wallet__uppertext--p">موجودی کیف پول</p>
                    <img src="../../assets/images/Wallet.svg" alt="" width="19px" height="17px">
                </div>
                <div class="wallet__asset d-flex justify-content-center align-items-center">
                    <p class="wallet__asset--amount">{{ total }}</p>
                    <span class="wallet__asset--unit align-self-start ms-1">تومان</span>
                </div>
            </div>

            <a href="#" data-bs-toggle="modal" data-bs-target="#wallet-modal"
                class="panel__wallet--add-asset d-inline-flex justify-content-center align-items-center p-3">
                افزایش موجودی
                <img src="../../assets/images/bx-alarm-add.svg" alt="" class="ms-2" width="24px" height="24px">
            </a>
            <div class="modal fade" id="wallet-modal" style="z-index: 101000" tabindex="-1"
                aria-labelledby="wallet-modal-label" aria-hidden="true">
                <div class="modal-dialog modal-dialog-centered">
                    <form method="POST">
                        <div class="modal-content">
                            <div class="modal-header">
                                <p class="modal-title" id="wallet-modal-label">فرم افزایش موجودی کیف پول</p>
                                <button type="button" class="btn-close" data-bs-dismiss="modal"
                                    aria-label="Close"></button>
                            </div>
                            <div class="modal-body">
                                <div class="row">
                                    <label class="form-label col-md-12 mb-3">لطفا مبلغ شارژ را به تومان وارد نمایید<span
                                            class="form-input-required">*</span></label>
                                    <div class="col-md-12">
                                        <input type="text" name="price" v-model="amount" id="form-input-wallet-price"
                                            class="form-control">
                                        <div class="text-danger text-start" v-if="Object.hasOwn(errors, 'price')">{{
                                            errors.price.shift() }}</div>
                                    </div>
                                </div>
                            </div>
                            <div class="modal-footer justify-content-end">
                                <div v-if="creditLoading" class=" spinner-border spinner-border-sm">

                                </div>
                                <button v-else type="submit" @click.prevent="creditWallet()"
                                    class="btn btn-success btn-sm btn-wallet-form-submit">واریز</button>
                                <button type="button" class="btn btn-secondary btn-sm"
                                    data-bs-dismiss="modal">انصراف</button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
        <div class="wallet__history w-100 d-inline-block mt-5">
            <div class="wallet__history--top w-100 d-flex flex-wrap align-items-center">
                <img src="../../assets/images/Group-39078.svg" alt="" class="wallet__history--top-icon me-3"
                    width="23px" height="23px">
                <h3 class="wallet__history--top-text">تراکنش ها</h3>

                <a href="#" @click.prevent="download()"
                    class="wallet__history--top-btn d-inline-flex justify-content-center align-items-center ms-auto border-0 px-2">
                    <div v-if="downloadLoading" class="spinner-border spinner-border-sm"></div>
                    <template v-else>
                        PDF
                        <img src="../../assets/images/Downlaodpdf.svg" alt="" class="ms-2" width="11px" height="9px">
                    </template>
                </a>
            </div>
            <ol class="wallet__history--list my-3 px-1 py-3">
                <li v-for="(transaction, tk) in transactions" :key="tk"
                    class="wallet__history--item d-lg-flex justify-content-between align-items-center py-3">
                    <span class="wallet__history--col wallet__history--itemno me-2">{{ tk + 1 }}</span>
                    <span class="wallet__history--col wallet__history--price mx-2">
                        <span class="wallet__history--col-span wallet__history--unit">تومان</span>
                        <span class="wallet__history--col-span wallet__history--priceno">{{ transaction.amount }}</span>
                    </span>
                    <span class="wallet__history--col wallet__history--date mx-2">{{ transaction.transaction_time ?
                        transaction.transaction_time.split(' ').shift() : '' }}</span>
                    <span class="wallet__history--col wallet__history--time mx-2">{{ transaction.transaction_time ?
                        transaction.transaction_time.split(' ').pop() : '' }}</span>
                    <span class="wallet__history--col wallet__history--time mx-2">{{ transaction.type == 'credit' ?
                        'واریز' :
                        'برداشت' }}</span>
                    <span class="wallet__history--col wallet__history--code mx-2">
                        {{ transaction.ref_id }}
                    </span>
                    <span class="wallet__history--col wallet__history--status mx-2">
                        موفق
                    </span>
                </li>
            </ol>
        </div>
    </div>
</template>
<script setup>
import { reactive, ref, computed } from "vue";
import { useToast } from "vue-toastification";
import usersApi from '@/axios/api'
import { useHead } from '@vueuse/head'
import { useUserStore } from '../../store/user'
import { handleError } from "@/utils/helpers";
import { useRouter } from "vue-router";
const router = useRouter()
const toast = useToast();
const userStore = useUserStore()
let transactions = ref([])
let total = ref(0)
let amount = ref(0)
let errors = reactive({})
let loading = ref(false)
let creditLoading = ref(false)
let downloadLoading = ref(false)
useHead({
    // Can be static or computed
    title: computed(() => `${userStore.getUser.name} ${userStore.getUser.family}- کیف پول`),
})
async function index() {
    loading.value = true;
    try {
        const response = await usersApi.post(`user/panel/transactions`);
        if (response.data.status == 'success') {
            transactions.value = response.data.data.transactions
            total.value = response.data.data.walletTotal
        }
    } catch (err) {
        toast.error(handleError(err).join(', '), {
            timeout: 2000
        });
    } finally {
        loading.value = false
    }
}
index()
async function creditWallet() {
    creditLoading.value = true;
    try {
        let url = `user/panel/credit/wallet`;
        const response = await usersApi.post(url, {
            'price': amount.value,
            'platform': 'web'
        });
        if (response.data.status == 'success') {
            router.push(response.data.data.payment_url)
        }
    } catch (err) {
        toast.error(handleError(err).join(', '), {
            timeout: 2000
        });
    } finally {
        creditLoading.value = false
    }
}
async function download() {
    try {
        downloadLoading.value = true;
        const response = await usersApi.get(`user/panel/wallet/transactions/pdf/download`, {
            responseType: "blob",
        });
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", 'tickets.pdf');
        document.body.appendChild(link);
        link.click();
        link.remove();
    } catch (err) {
        toast.error(handleError(err).join(', '), {
            timeout: 2000
        });
    } finally {
        downloadLoading.value = false
    }
}
</script>
